.App {
  text-align: center;
}

.App-logo {
  height: 100px;
  pointer-events: none;
  position: relative;
  top: 2px;
  left: 2px;
  float: left;
}

.Platzsparend {
  position: absolute;
  left: 500px;
  top: 2px;  
  font-size: 30px;
  font-style: italic;
  font-family: "Source Sans Pro";
}

.App-header {
  background-color: #e4eef1;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.Nachricht {
  align-self: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
